}

@media (min-width: 768px) {
  .o-layout--2 > .o-layout__item {
    width: 50%;
    flex-basis: 50%;
  }
}

@media (min-width: 768px) {
  .o-layout--3 > .o-layout__item {
    width: 32%;
  }
}

@media (min-width: 768px) {
  .o-layout--4 > .o-layout__item {
    width: 24%;
