  margin-bottom: 1em;
}

.c-comment__author {
  font-size: 1rem;
  margin-bottom: 0.25em;
}

.c-comment__moderation {
  font-style: italic;
  margin-bottom: 0.5em;
}

/* ==========================================================================
   #FOOTER
   ========================================================================== */
.c-footer {
  border: 1px solid #ccc;
  color: #666;
  font-size: 0.8rem;
  margin: 3em auto;
  max-width: 800px;
  padding: 2.5em 1em 1em 1em;
  text-align: center;
}

.c-footer__note {
  margin-bottom: 0.5em;
}

.c-footer__list {
  list-style: none;
}

.c-footer__list__item {
  display: inline-block;
  margin: 0 0.25em;
}

.c-footer__xfive:before {
  background: #ccc;
  content: '';
  display: block;
  height: 1px;
  margin: 0 auto 1em auto;
  width: 3em;
}

/* ==========================================================================
   #PERSON
   ========================================================================== */
.c-person__info {
  padding: 4em 4em 1em 4em;
}

.c-person__info__name {
  font-size: 3.6rem;
}

.c-person__info__position {
