  display: inline-block;
  font-size: 1.8rem;
  font-weight: normal;
  margin-bottom: 1em;
  padding-bottom: 1em;
}

.c-person__info__description {
  width: 75%;
}

.c-person__quote {
  padding: 4em;
}

.c-person__quote__title {
  font-weight: bold;
  font-size: 1rem;
}

.c-person__quote__text {
  quotes: '“' '”' '‘' '’';
}

.c-person__quote__text p {
  color: #999;
  font-size: 1.5rem;
}

.c-person__quote__text p:before {
  content: open-quote;
}

.c-person__quote__text p:after {
  content: close-quote;
}

.c-person__links {
  background: #333;
  color: #fff;
  padding: 4em 4em 8em 4em;
  position: relative;
}

.c-person__links__title {
  margin-bottom: 0.25em;
}

.c-person__links__list {
  margin-left: 0;
}

.c-person__links__item {
  list-style: none;
}

.c-person__links__link {
  color: #fff;
}

.c-person__links__nav {
  background: #999;
  bottom: 0;
  color: #fff;
  font-size: 1.5rem;
  left: 0;
  padding: 1em;
  position: absolute;
  text-align: center;
  text-decoration: none;
  width: 50%;
}

.c-person__links__nav:hover {
  color: #000;
}

.c-person__links__nav--next {
  background: #666;
  left: auto;
  right: 0;
}

